.footer {
  padding: 50px 0;
  background-color: #1a1a1a;
  color: #fff;
  display: flex;
  align-items: center; }

.footer__wrapper {
  width: 70%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 24px; }

.footer__back {
  display: flex;
  align-items: center;
  flex-direction: column;
  text-decoration: none;
  color: white;
  font-family: "Montserrat", "Arial", sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 15px;
  letter-spacing: 2px;
  text-align: center;
  gap: 6px;
  transition: 0.3s;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }
  .footer__back:hover {
    color: #ff34c0; }
  .footer__back:focus-visible {
    transition: none;
    outline-offset: 4px;
    color: #ff34c0;
    padding: 0 8px; }
  .footer__back:active {
    color: rgba(255, 52, 192, 0.5); }

.social {
  display: flex;
  justify-content: center;
  gap: 24px;
  list-style: none;
  padding: 0;
  margin: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

.social__link {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 0.2s;
  overflow: hidden; }
  .social__link:hover {
    color: #ff34c0;
    transform: scale(1.2); }
  .social__link:focus-visible {
    outline-offset: 2px;
    transition: none; }
  .social__link:active {
    color: rgba(255, 52, 192, 0.6); }

.footer__copyright {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Inter", "Arial", sans-serif; }

@keyframes shake {
  33% {
    transform: translateX(-15px); }
  66% {
    transform: translateX(15px); } }

/* Выпадание меню */
@keyframes bounce {
  0% {
    transform: translateY(-228px); }
  100% {
    transform: translateY(0); } }

.link {
  text-decoration: none;
  color: white;
  transition: 0.2s;
  cursor: pointer; }

.main-nav__link_current {
  color: #ff34c0; }
